.container {
    padding: 20px 0;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    flex-wrap: wrap;
    gap: 12px;
}

.title {
    font-size: 22px;
    font-weight: 700;
    color: #1a1a2e;
    margin: 0;
}

.count {
    font-size: 14px;
    color: #888;
    font-weight: 400;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 20px;
}

.grid a {
    text-decoration: none;
}

.card {
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(12px);
    border-radius: 16px;
    overflow: hidden;
    transition: all 0.3s ease;
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.card:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 32px rgba(0, 0, 0, 0.12);
}

.cardImage {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.cardBody {
    padding: 16px 20px;
}

.cardTitle {
    font-size: 17px;
    font-weight: 600;
    color: #1a1a2e;
    margin-bottom: 6px;
}

.cardMeta {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 10px;
    flex-wrap: wrap;
}

.cardTag {
    font-size: 12px;
    padding: 2px 10px;
    border-radius: 20px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: #fff;
}

.cardLocation {
    font-size: 13px;
    color: #888;
    display: flex;
    align-items: center;
    gap: 4px;
}

.cardPrice {
    font-size: 18px;
    font-weight: 700;
    color: #52c41a;
}

.cardPrice sup {
    font-size: 12px;
    font-weight: 500;
    color: #999;
    margin-left: 2px;
}

.cardFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.badges {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
}

.badgeItem {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 12px;
    color: #666;
    background: rgba(0, 0, 0, 0.04);
    padding: 4px 10px;
    border-radius: 20px;
}

.empty {
    text-align: center;
    padding: 80px 20px;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
    border-radius: 16px;
}

.emptyIcon {
    font-size: 64px;
    color: #d9d9d9;
    margin-bottom: 16px;
}

.emptyText {
    font-size: 18px;
    color: #999;
    margin-bottom: 8px;
}

.emptyHint {
    font-size: 14px;
    color: #bbb;
}

@media (max-width: 768px) {
    .grid {
        grid-template-columns: 1fr;
    }

    .header {
        flex-direction: column;
        align-items: flex-start;
    }

    .title {
        font-size: 18px;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 12px 0;
    }

    .cardImage {
        height: 160px;
    }

    .cardBody {
        padding: 12px 14px;
    }
}
