.container {
    width: 100%;
    padding: 24px;
}

.section {
    margin-bottom: 24px;
}

.glassCard {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    box-shadow: 0 4px 24px -1px rgba(0, 0, 0, 0.05);
    height: 100%;
    transition: all 0.3s ease;
}

.glassCard:hover {
    box-shadow: 0 8px 32px -4px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}

.glassCard :global(.ant-card-head) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    padding: 0 24px;
    min-height: 56px;
}

.glassCard :global(.ant-card-head-title) {
    font-size: 16px;
    font-weight: 600;
    color: #2b2b2b;
    padding: 16px 0;
}

.glassCard :global(.ant-card-body) {
    padding: 24px;
}

.pageHeader {
    margin-bottom: 24px;
}

.pageTitle {
    font-size: 24px;
    font-weight: 700 !important;
    color: #2b2b2b !important;
    margin: 0 !important;
}

.pageSubtitle {
    color: #666;
    font-size: 14px;
}

.uploadDragger {
    background: rgba(255, 255, 255, 0.5) !important;
    border: 2px dashed rgba(0, 0, 0, 0.1) !important;
    border-radius: 12px !important;
    transition: all 0.3s !important;
}

.uploadDragger:hover {
    border-color: #1890ff !important;
    background: rgba(24, 144, 255, 0.05) !important;
}

.devicesList {
    height: 70vh;
    overflow: auto;
    padding-right: 8px;
}

.deviceItem {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    padding: 12px;
    margin-bottom: 12px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    transition: all 0.3s;
}

.deviceItem:hover {
    background: rgba(255, 255, 255, 0.8);
    transform: translateX(4px);
}

/* Profile Header Card */
.profileHeaderCard {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    box-shadow: 0 4px 24px -1px rgba(0, 0, 0, 0.05);
    margin-bottom: 24px;
}

.profileHeaderContent {
    display: flex;
    align-items: center;
    gap: 32px;
}

.avatarSection {
    flex-shrink: 0;
}

.infoSection {
    flex: 1;
}

.nameInput {
    margin-bottom: 0 !important;
}

.profileNameInput {
    font-size: 24px !important;
    font-weight: 700 !important;
    color: #2b2b2b !important;
    padding: 0 !important;
    background: transparent !important;
}

.profileNameInput::placeholder {
    color: rgba(0, 0, 0, 0.25);
}

.phoneInput {
    margin-bottom: 0 !important;
}

.profilePhoneInput {
    font-size: 16px !important;
    color: #666 !important;
    padding: 0 !important;
    background: transparent !important;
}

/* Responsive */
@media (max-width: 992px) {
    .container {
        padding: 16px;
    }
}

@media (max-width: 768px) {
    .profileHeaderContent {
        flex-direction: column;
        text-align: center;
        gap: 24px;
    }

    .profileNameInput {
        text-align: center;
        font-size: 20px !important;
    }

    .profilePhoneInput {
        text-align: center;
    }

    .devicesList {
        height: auto;
        margin-top: 24px;
    }
}