.app {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
}

button {
    padding: 10px 15px;
    border: none;
    background-color: teal;
    color: #fff;
    border-radius: 10px;
}


.blurshadowbox {
    background-color: #ffffff71;
    backdrop-filter: blur(30px);
    border-radius: 15px;
    border-top: .5px solid #fff;
    border-right: .5px solid #fff;
    box-shadow: 0px 0px 20px -10px #47474767;
}


.cardbox {
    padding: 25px;
}

.heading {
    color: teal;
    margin-bottom: 20px;
}

.heading span {
    color: red;
}

.restcard {
    background-color: #fff;
    padding: 0px;
    border-radius: 15px;
    box-shadow: 0px 0px 20px -10px #47474767;
}

.restcard .mainimg {
    border-radius: 20px 20px 0px 0px;
}

.day {
    background-color: lightgray;
    border-radius: 15px;
}

.two {
    margin: 10px 0;
    display: grid;
    gap: 5px;
    grid-template-columns: repeat(4, 1fr);
}

.two .udob {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 3px;
    padding: 3px;
    border-radius: 5px;
    align-items: center;
    font-weight: 800;
}

.one {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}

.resname {
    color: teal;
    font-weight: 800;
}

.resprice {
    color: blue;
}

.resprice sup {
    font-size: 10px;
}

.singerslidecard {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px 12px;
    background: linear-gradient(145deg, #ffffff 0%, #f8f9fa 100%);
    border-radius: 20px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
}

.singerslidecard::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 50%;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border-radius: 20px 20px 50% 50%;
    opacity: 0.1;
    transition: opacity 0.3s ease;
}

.singerslidecard:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 30px rgba(102, 126, 234, 0.25);
}

.singerslidecard:hover::before {
    opacity: 0.2;
}

.singerslidecard .rating {
    position: absolute;
    top: 10px;
    right: 10px;
    background: linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%);
    color: #fff;
    padding: 4px 8px;
    border-radius: 12px;
    font-size: 11px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 3px;
    box-shadow: 0 2px 8px rgba(238, 90, 36, 0.3);
    z-index: 2;
}

.singerslidecard .rating svg {
    width: 10px;
    height: 10px;
}

.singerslidecard .imgwrap {
    position: relative;
    z-index: 1;
    padding: 3px;
    /* background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
    border-radius: 50%;
    margin-bottom: 12px;
}

.singerslidecard img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
    border: 3px solid #fff;
    display: block;
}

.singerslidecard .info {
    text-align: center;
    z-index: 1;
}

.singerslidecard .name {
    font-size: 13px;
    font-weight: 600;
    color: #2d3436;
    margin: 0 0 4px;
    line-height: 1.3;
}

.singerslidecard .genre {
    font-size: 11px;
    color: #636e72;
    margin: 0;
}

@media (max-width: 768px) {
    .singerslidecard {
        padding: 12px 8px;
    }
    .singerslidecard img {
        width: 65px;
        height: 65px;
    }
    .singerslidecard .name {
        font-size: 12px;
    }
}

@media (max-width: 960px) {
    .site {
        display: grid;
        grid-template-columns: 1fr;
        height: 100vh;
        grid-template-rows: auto 1fr;
        grid-template-areas: "head" "main";
        gap: 10px;
        backdrop-filter: blur(20px);
    }

    .site main {
        padding-bottom: 80px;
    }
}

/* Badge Utility Classes */
.badge {
    display: flex;
    gap: 8px;
    align-items: center;
    padding: 8px;
    border-radius: 8px;
    transition: all 0.3s;
}

.badge:hover {
    background: rgba(0, 0, 0, 0.05);
}

.badge-green {
    background: rgba(82, 196, 26, 0.1) !important;
    color: var(--success-color);
}

.badge-red {
    background: rgba(255, 77, 79, 0.1) !important;
    color: var(--error-color);
}

.badge-blue {
    background: rgba(24, 144, 255, 0.1) !important;
    color: #1890ff;
}

.badge-orange {
    background: rgba(250, 173, 20, 0.1) !important;
    color: var(--warning-color);
}

.badge span:first-child {
    font-size: 14px;
    font-weight: 700;
}

.badge span:last-child {
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 600;
    opacity: 0.8;
}

/* Header User Menu */
.header-user-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: transparent;
    padding: 0;
}

.header-user-name {
    margin-right: 12px;
    font-weight: 600;
    color: var(--text-color);
}

.header-login-btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primary-color);
    color: white;
    box-shadow: 0 2px 8px rgba(102, 126, 234, 0.4);
    transition: all 0.3s;
}

.header-login-btn:hover {
    transform: scale(1.1);
}

.testbg {
    color: red !important;
}