.dashboard {
    width: 100%;
    padding: 16px;
}

/* Loading */
.loadingWrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    min-height: 300px;
    color: #888;
    font-size: 14px;
}

.spinner {
    width: 36px;
    height: 36px;
    border: 3px solid #f0f0f0;
    border-top-color: #1890ff;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

/* Page Header */
.pageHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    flex-wrap: wrap;
    gap: 12px;
}

.pageTitle {
    margin: 0;
    font-size: 22px;
    font-weight: 700;
    color: #1a1a2e;
}

.pageSubtitle {
    margin: 4px 0 0;
    font-size: 13px;
    color: #888;
}

.todayBadge {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 16px;
    background: #f0f5ff;
    border-radius: 20px;
    font-size: 13px;
    color: #1890ff;
    font-weight: 500;
}

/* Stats Grid */
.statsGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;
    margin-bottom: 20px;
}

.statCard {
    border-radius: 16px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    transition: transform 0.2s, box-shadow 0.2s;
    cursor: default;
    position: relative;
    overflow: hidden;
}

.statCard::before {
    content: '';
    position: absolute;
    top: -20px;
    right: -20px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    opacity: 0.1;
}

.statCard:hover {
    transform: translateY(-3px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.statBlue {
    background: linear-gradient(135deg, #e6f7ff 0%, #bae7ff 100%);
    border: 1px solid #91d5ff;
}
.statBlue::before { background: #1890ff; }
.statBlue .statIconWrap { color: #1890ff; background: rgba(24, 144, 255, 0.12); }

.statGreen {
    background: linear-gradient(135deg, #f6ffed 0%, #d9f7be 100%);
    border: 1px solid #b7eb8f;
}
.statGreen::before { background: #52c41a; }
.statGreen .statIconWrap { color: #52c41a; background: rgba(82, 196, 26, 0.12); }

.statPurple {
    background: linear-gradient(135deg, #f9f0ff 0%, #efdbff 100%);
    border: 1px solid #d3adf7;
}
.statPurple::before { background: #722ed1; }
.statPurple .statIconWrap { color: #722ed1; background: rgba(114, 46, 209, 0.12); }

.statOrange {
    background: linear-gradient(135deg, #fff7e6 0%, #ffe7ba 100%);
    border: 1px solid #ffd591;
}
.statOrange::before { background: #fa8c16; }
.statOrange .statIconWrap { color: #fa8c16; background: rgba(250, 140, 22, 0.12); }

.statIconWrap {
    width: 42px;
    height: 42px;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.statBody {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.statLabel {
    font-size: 12px;
    color: #666;
    font-weight: 500;
}

.statValue {
    font-size: 24px;
    font-weight: 700;
    color: #1a1a2e;
    line-height: 1.2;
}

.statMini {
    display: flex;
    gap: 8px;
}

.statMiniItem {
    font-size: 11px;
    color: #888;
    display: flex;
    align-items: center;
    gap: 4px;
}

/* Finance Row */
.financeRow {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 20px;
    background: #fff;
    border-radius: 16px;
    border: 1px solid #f0f0f0;
    margin-bottom: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.financeCard {
    display: flex;
    align-items: center;
    gap: 12px;
    flex: 1;
    min-width: 140px;
}

.financeProfit {
    background: #f6ffed;
    padding: 12px 16px;
    border-radius: 12px;
}

.financeIcon {
    width: 40px;
    height: 40px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    flex-shrink: 0;
}

.financeInfo {
    display: flex;
    flex-direction: column;
}

.financeLabel {
    font-size: 11px;
    color: #999;
    font-weight: 500;
}

.financeValue {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
}

.financeDivider {
    font-size: 18px;
    font-weight: 700;
    color: #d9d9d9;
    padding: 0 4px;
}

/* Status Row */
.statusRow {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 12px;
    margin-bottom: 24px;
}

.statusCard {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 16px;
    background: #fff;
    border-radius: 12px;
    border: 1px solid #f0f0f0;
}

.statusIcon {
    font-size: 20px;
}

.statusInfo {
    display: flex;
    flex-direction: column;
}

.statusCount {
    font-size: 20px;
    font-weight: 700;
    color: #1a1a2e;
    line-height: 1.2;
}

.statusLabel {
    font-size: 11px;
    color: #888;
}

/* Content Grid */
.contentGrid {
    display: grid;
    grid-template-columns: 1fr 380px;
    gap: 20px;
}

.sideColumn {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* Section */
.section {
    background: #fff;
    border-radius: 16px;
    border: 1px solid #f0f0f0;
    padding: 20px;
}

.sectionTitle {
    margin: 0 0 16px;
    font-size: 15px;
    font-weight: 600;
    color: #1a1a2e;
    display: flex;
    align-items: center;
    gap: 8px;
}

/* Orders List */
.ordersList {
    display: flex;
    flex-direction: column;
}

.orderItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 0;
    border-bottom: 1px solid #f5f5f5;
    gap: 12px;
}

.orderItem:last-child {
    border-bottom: none;
}

.orderLeft {
    display: flex;
    align-items: center;
    gap: 12px;
    flex: 1;
    min-width: 0;
}

.orderInfo {
    min-width: 0;
}

.orderName {
    font-weight: 600;
    font-size: 14px;
    color: #1a1a2e;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.orderMeta {
    display: flex;
    gap: 12px;
    font-size: 12px;
    color: #888;
    margin-top: 2px;
}

.orderRight {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 4px;
    flex-shrink: 0;
}

.orderAmount {
    font-size: 14px;
    font-weight: 700;
    color: #1a1a2e;
}

/* Employee List */
.empList {
    display: flex;
    flex-direction: column;
}

.empItem {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 0;
    border-bottom: 1px solid #f5f5f5;
}

.empItem:last-child {
    border-bottom: none;
}

.empRank {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: #f0f5ff;
    color: #1890ff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    font-weight: 700;
    flex-shrink: 0;
}

.empInfo {
    flex: 1;
    min-width: 0;
}

.empName {
    font-weight: 600;
    font-size: 13px;
    color: #1a1a2e;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.empPosition {
    font-size: 11px;
    color: #999;
}

.empStats {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-shrink: 0;
}

.empCount {
    font-size: 11px;
    color: #888;
}

.empTotal {
    font-size: 13px;
    font-weight: 600;
    color: #1890ff;
}

/* Service List */
.serviceList {
    display: flex;
    flex-direction: column;
}

.serviceItem {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 10px 0;
    border-bottom: 1px solid #f5f5f5;
}

.serviceItem:last-child {
    border-bottom: none;
}

.serviceThumb {
    width: 44px;
    height: 44px;
    border-radius: 10px;
    background: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    flex-shrink: 0;
    font-size: 18px;
    color: #bbb;
}

.serviceThumb img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.serviceInfo {
    flex: 1;
    min-width: 0;
}

.serviceName {
    font-weight: 600;
    font-size: 13px;
    color: #1a1a2e;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.servicePrice {
    font-size: 12px;
    color: #52c41a;
    font-weight: 600;
}

/* Responsive */
@media (max-width: 1200px) {
    .contentGrid {
        grid-template-columns: 1fr;
    }

    .sideColumn {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 1024px) {
    .statsGrid {
        grid-template-columns: repeat(2, 1fr);
    }

    .statusRow {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .dashboard {
        padding: 12px;
    }

    .statsGrid {
        grid-template-columns: 1fr 1fr;
        gap: 10px;
    }

    .statCard {
        padding: 14px;
    }

    .statValue {
        font-size: 20px;
    }

    .financeRow {
        flex-direction: column;
        gap: 12px;
        padding: 16px;
    }

    .financeCard {
        width: 100%;
    }

    .financeDivider {
        display: none;
    }

    .statusRow {
        grid-template-columns: 1fr 1fr;
        gap: 8px;
    }

    .contentGrid {
        grid-template-columns: 1fr;
    }

    .sideColumn {
        grid-template-columns: 1fr;
    }

    .orderMeta {
        flex-direction: column;
        gap: 2px;
    }
}

@media (max-width: 480px) {
    .statsGrid {
        grid-template-columns: 1fr;
    }

    .statusRow {
        grid-template-columns: 1fr;
    }

    .pageTitle {
        font-size: 18px;
    }

    .financeValue {
        font-size: 14px;
    }
}