/* ===== GALLERY ===== */
.gallery-main {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
}

.gallery-main .ant-image {
  border-radius: 16px;
  overflow: hidden;
}

.gallery-thumbs {
  display: flex;
  gap: 8px;
  margin-top: 12px;
  overflow-x: auto;
  padding-bottom: 4px;
}

.gallery-thumb {
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  border: 3px solid transparent;
  transition: border-color 0.2s, transform 0.2s;
  flex-shrink: 0;
}

.gallery-thumb:hover {
  transform: scale(1.05);
}

.gallery-thumb-active {
  border-color: #1890ff;
}

/* ===== INFO CARDS ===== */
.info-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  margin: 20px 0;
}

.info-card {
  padding: 16px;
  border-radius: 14px;
  text-align: center;
  transition: transform 0.2s;
}

.info-card:hover {
  transform: translateY(-2px);
}

.info-card-icon {
  font-size: 22px;
  margin-bottom: 6px;
}

.info-card-value {
  font-size: 20px;
  font-weight: 700;
  color: #1a1a2e;
}

.info-card-label {
  font-size: 11px;
  color: #888;
  margin-top: 2px;
}

/* ===== EXTRAS ===== */
.extras-list {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin: 16px 0;
}

.extra-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 18px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  transition: transform 0.2s, box-shadow 0.2s;
  flex: 1;
  min-width: 200px;
}

.extra-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 14px rgba(0,0,0,0.1);
}

/* ===== PROVIDER CARD ===== */
.provider-card {
  border-radius: 16px !important;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  margin-bottom: 16px;
}

/* ===== DESCRIPTION ===== */
.description-box {
  background: #fff;
  border-radius: 16px;
  padding: 24px;
  margin-top: 20px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  line-height: 1.9;
  font-size: 15px;
  color: #444;
}

/* ===== PRICING CARD ===== */
.pricing-card {
  position: sticky;
  top: 24px;
  border-radius: 16px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
}

.pricing-card .ant-card-body {
  padding: 24px;
}

/* ===== DATE LINE ===== */
.dateline-box {
  margin-top: 20px;
  padding: 24px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.ordertimes {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

.orderdays {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  text-align: center;
}

/* ===== BREADCRUMB ===== */
.services-header {
  margin-bottom: 8px;
}

/* ===== RESPONSIVE ===== */
@media (max-width: 992px) {
  .pricing-card {
    position: relative;
    top: 0;
    margin-top: 24px;
  }

  .info-cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .info-cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
  }

  .info-card {
    padding: 12px;
  }

  .info-card-value {
    font-size: 16px;
  }

  .extras-list {
    flex-direction: column;
  }

  .extra-item {
    min-width: unset;
  }

  .gallery-main .ant-image img {
    height: 300px !important;
  }
}
