.container {
    width: 100%;
}

.card {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
}

.card :global(.ant-card-head) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.cardTitle {
    color: #2b2b2b;
    font-size: 18px;
    font-weight: 600;
}

.table :global(.ant-table) {
    background: transparent;
}

.table :global(.ant-table-thead > tr > th) {
    background: rgba(255, 255, 255, 0.05);
    color: #2b2b2b;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-weight: 600;
}

.table :global(.ant-table-tbody > tr > td) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    color: #2b2b2b;
}

.table :global(.ant-table-tbody > tr:hover > td) {
    background: rgba(255, 255, 255, 0.05);
}

.table :global(.ant-pagination) {
    color: #2b2b2b;
}

.table :global(.ant-pagination-item) {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.2);
}

.table :global(.ant-pagination-item a) {
    color: #2b2b2b;
}

.table :global(.ant-pagination-item-active) {
    background: #1890ff;
    border-color: #1890ff;
}

.table :global(.ant-pagination-item-active a) {
    color: #2b2b2b;
}

.viewContent {
    color: rgba(0, 0, 0, 0.85);
}

.viewSection {
    margin-bottom: 24px;
}

.viewSection h3 {
    margin-bottom: 12px;
    font-size: 16px;
    font-weight: 600;
}

.viewSection p {
    margin-bottom: 8px;
}

/* Responsive breakpoints */
@media (max-width: 768px) {
    .cardTitle {
        font-size: 16px;
    }

    .card :global(.ant-card-extra) {
        margin-top: 12px;
    }

    .table :global(.ant-table) {
        font-size: 12px;
    }

    .table :global(.ant-table-thead > tr > th),
    .table :global(.ant-table-tbody > tr > td) {
        padding: 8px;
    }
}

@media (max-width: 576px) {
    .card {
        border-radius: 8px;
    }

    .cardTitle {
        font-size: 14px;
    }

    .table :global(.ant-table) {
        font-size: 11px;
    }

    .table :global(.ant-table-thead > tr > th),
    .table :global(.ant-table-tbody > tr > td) {
        padding: 6px 4px;
    }
}