.footer {
    background: linear-gradient(135deg, #1a2e23 0%, #163e24 50%, #0f602a 100%);
    color: #fff;
    padding: 60px;
    margin-top: 30px;
    position: relative;
    overflow: hidden;
    border-radius: 30px;
}

.footer::before {
    content: '';
    position: absolute;
    top: -100px;
    left: -100px;
    width: 300px;
    height: 300px;
    background: radial-gradient(circle, rgba(24, 144, 255, 0.08) 0%, transparent 70%);
    border-radius: 50%;
}

.footer::after {
    content: '';
    position: absolute;
    bottom: -80px;
    right: -80px;
    width: 250px;
    height: 250px;
    background: radial-gradient(circle, rgba(82, 196, 26, 0.06) 0%, transparent 70%);
    border-radius: 50%;
}

.footerInner {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    z-index: 1;
}

.footerTop {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1.5fr;
    gap: 40px;
    padding-bottom: 40px;
}

/* Brand */
.brand {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.brandLogo {
    display: flex;
    align-items: center;
    gap: 12px;
}

.brandLogo img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
}

.brandLogo span {
    font-size: 22px;
    font-weight: 700;
    letter-spacing: -0.5px;
}

.brandDesc {
    font-size: 14px;
    line-height: 1.7;
    color: rgba(255, 255, 255, 0.6);
    max-width: 320px;
}

.socials {
    display: flex;
    gap: 10px;
    margin-top: 8px;
}

.socialBtn {
    width: 38px;
    height: 38px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.08);
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(255, 255, 255, 0.7);
    font-size: 16px;
    transition: all 0.3s;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.06);
}

.socialBtn:hover {
    background: #1890ff;
    color: #fff;
    transform: translateY(-2px);
}

/* Columns */
.colTitle {
    font-size: 15px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 20px;
    position: relative;
    padding-bottom: 10px;
}

.colTitle::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 30px;
    height: 2px;
    background: #1890ff;
    border-radius: 2px;
}

.linkList {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.linkList li a {
    color: rgba(255, 255, 255, 0.55);
    text-decoration: none;
    font-size: 14px;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 8px;
}

.linkList li a:hover {
    color: #1890ff;
    transform: translateX(4px);
}

/* Contact */
.contactItem {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    margin-bottom: 16px;
}

.contactIcon {
    width: 36px;
    height: 36px;
    border-radius: 10px;
    background: rgba(24, 144, 255, 0.12);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #1890ff;
    font-size: 16px;
    flex-shrink: 0;
}

.contactText {
    display: flex;
    flex-direction: column;
}

.contactText span:first-child {
    font-size: 11px;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.contactText span:last-child {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.85);
    margin-top: 2px;
}

.contactText a {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.85);
    text-decoration: none;
    transition: color 0.2s;
}

.contactText a:hover {
    color: #1890ff;
}

/* Bottom */
.footerBottom {
    border-top: 1px solid rgba(255, 255, 255, 0.08);
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
}

.copyright {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.4);
}

.copyright a {
    color: #1890ff;
    text-decoration: none;
}

.bottomLinks {
    display: flex;
    gap: 20px;
}

.bottomLinks a {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.4);
    text-decoration: none;
    transition: color 0.2s;
}

.bottomLinks a:hover {
    color: #1890ff;
}

/* Responsive */
@media (max-width: 992px) {
    .footerTop {
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
}

@media (max-width: 960px) {
    .footer {
        display: none;
    }
}

@media (min-width: 961px) and (max-width: 992px) {
    .footerTop {
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
}
