.container {
    width: 100%;
    padding: 16px;
}

/* Page Header */
.pageHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    flex-wrap: wrap;
    gap: 12px;
}

.pageTitle {
    margin: 0;
    font-size: 22px;
    font-weight: 700;
    color: #1a1a2e;
}

.pageSubtitle {
    margin: 4px 0 0;
    font-size: 13px;
    color: #888;
}

/* Stats Grid */
.statsGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;
    margin-bottom: 24px;
}

.statCard {
    border-radius: 16px;
    padding: 20px;
    display: flex;
    align-items: center;
    gap: 16px;
    transition: transform 0.2s, box-shadow 0.2s;
    cursor: default;
}

.statCard:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.statIncome {
    background: linear-gradient(135deg, #e6f7ff 0%, #bae7ff 100%);
    border: 1px solid #91d5ff;
}

.statOylik {
    background: linear-gradient(135deg, #f0f5ff 0%, #d6e4ff 100%);
    border: 1px solid #adc6ff;
}

.statExpense {
    background: linear-gradient(135deg, #fff1f0 0%, #ffccc7 100%);
    border: 1px solid #ffa39e;
}

.statProfit {
    background: linear-gradient(135deg, #f6ffed 0%, #d9f7be 100%);
    border: 1px solid #b7eb8f;
}

.statIcon {
    width: 48px;
    height: 48px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    background: rgba(255, 255, 255, 0.7);
    flex-shrink: 0;
}

.statIncome .statIcon { color: #1890ff; }
.statOylik .statIcon { color: #597ef7; }
.statExpense .statIcon { color: #ff4d4f; }
.statProfit .statIcon { color: #52c41a; }

.statInfo {
    display: flex;
    flex-direction: column;
    min-width: 0;
}

.statLabel {
    font-size: 12px;
    color: #666;
    font-weight: 500;
}

.statValue {
    font-size: 20px;
    font-weight: 700;
    color: #1a1a2e;
    line-height: 1.2;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.statSuffix {
    font-size: 11px;
    color: #999;
}

/* Progress Section */
.progressSection {
    background: #fff;
    border-radius: 16px;
    padding: 20px;
    margin-bottom: 20px;
    border: 1px solid #f0f0f0;
}

.progressHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 14px;
    color: #333;
}

.progressTotal {
    color: #1890ff;
}

.progressBar {
    height: 12px;
    background: #f5f5f5;
    border-radius: 6px;
    display: flex;
    overflow: hidden;
    margin-bottom: 12px;
}

.progressFill {
    height: 100%;
    transition: width 0.6s ease;
    min-width: 2px;
}

.progressFill:first-child {
    border-radius: 6px 0 0 6px;
}

.progressFill:last-child {
    border-radius: 0 6px 6px 0;
}

.progressLegend {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.legendItem {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 12px;
    color: #666;
}

.legendDot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    flex-shrink: 0;
}

/* Mini Stats */
.miniStats {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
    margin-bottom: 8px;
}

.miniStatItem {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 13px;
    color: #555;
    background: #fafafa;
    padding: 8px 16px;
    border-radius: 20px;
    border: 1px solid #f0f0f0;
}

.miniStatItem :global(.anticon) {
    color: #1890ff;
}

/* Section Title */
.sectionTitle {
    font-size: 16px;
    font-weight: 600;
    color: #1a1a2e;
    margin-bottom: 16px;
}

/* Collapse */
.orderCollapse {
    background: transparent;
    border: none;
}

.orderCollapse :global(.ant-collapse-item) {
    border: 1px solid #f0f0f0 !important;
    border-radius: 12px !important;
    margin-bottom: 12px;
    overflow: hidden;
    background: #fff;
}

.orderCollapse :global(.ant-collapse-header) {
    padding: 14px 16px !important;
}

.orderCollapse :global(.ant-collapse-content-box) {
    padding: 0 16px 16px !important;
}

/* Collapse Header */
.collapseHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 12px;
}

.collapseLeft {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
}

.collapseDate {
    font-size: 13px;
    font-weight: 600;
    color: #333;
}

.collapseTime {
    font-size: 12px;
    color: #888;
}

.collapseRight {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 2px;
}

.collapseAmount {
    font-size: 14px;
    font-weight: 700;
    color: #1a1a2e;
}

.collapseFoyda {
    font-size: 12px;
    font-weight: 600;
}

/* Order Detail */
.orderDetail {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.detailRow {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;
    background: #fafafa;
    padding: 14px;
    border-radius: 10px;
}

.detailItem {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.detailItem label {
    font-size: 11px;
    color: #999;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.3px;
}

.detailItem span {
    font-size: 14px;
    font-weight: 600;
    color: #333;
}

.detailSection {
    background: #fafafa;
    border-radius: 10px;
    padding: 14px;
}

.detailSection h4 {
    margin: 0 0 10px;
    font-size: 13px;
    font-weight: 600;
    color: #555;
}

.detailListItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #f0f0f0;
    font-size: 13px;
}

.detailListItem:last-of-type {
    border-bottom: none;
}

.detailTotal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    margin-top: 6px;
    border-top: 1px dashed #d9d9d9;
    font-weight: 600;
    font-size: 13px;
}

.detailProfit {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 16px;
    border-radius: 10px;
    font-weight: 600;
}

/* Responsive */
@media (max-width: 1024px) {
    .statsGrid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .container {
        padding: 12px;
    }

    .statsGrid {
        grid-template-columns: 1fr 1fr;
        gap: 10px;
    }

    .statCard {
        padding: 14px;
        gap: 10px;
    }

    .statIcon {
        width: 40px;
        height: 40px;
        font-size: 18px;
    }

    .statValue {
        font-size: 16px;
    }

    .detailRow {
        grid-template-columns: 1fr;
        gap: 8px;
    }

    .collapseHeader {
        flex-direction: column;
        align-items: flex-start;
    }

    .collapseRight {
        align-items: flex-start;
        flex-direction: row;
        gap: 10px;
    }

    .miniStats {
        gap: 8px;
    }

    .miniStatItem {
        padding: 6px 12px;
        font-size: 12px;
    }
}

@media (max-width: 480px) {
    .statsGrid {
        grid-template-columns: 1fr;
    }

    .pageTitle {
        font-size: 18px;
    }

    .progressLegend {
        gap: 12px;
    }
}