.sidebar {
    height: 100%;
}

.logo {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    margin-bottom: 16px;
}

.logoFull {
    display: flex;
    align-items: center;
    gap: 12px;
    width: 100%;
}

.logoIcon {
    width: 48px;
    height: 48px;
    background: linear-gradient(135deg, #1890ff 0%, #096dd9 100%);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 700;
    color: #2b2b2b;
}

.logoText {
    flex: 1;
}

.logoTitle {
    color: 2b2b2b;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
}

.logoSubtitle {
    color: rgba(255, 255, 255, 0.65);
    font-size: 12px;
    line-height: 1.2;
}

.logoCollapsed {
    width: 40px;
    height: 40px;
    background: linear-gradient(135deg, #1890ff 0%, #096dd9 100%);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 700;
    color: 2b2b2b;
}

.menu {
    background: transparent !important;
    border-right: none !important;
}

.menu :global(.ant-menu-item),
.menu :global(.ant-menu-submenu-title) {
    color: #2b2b2b !important;
    font-size: 15px;
    margin: 4px 8px;
    border-radius: 8px;
    height: 44px;
    line-height: 44px;
}

.menu :global(.ant-menu-item-selected) {
    background: linear-gradient(135deg, #1890ff 0%, #096dd9 100%) !important;
    color: #fff !important;
}

.menu :global(.ant-menu-item:hover),
.menu :global(.ant-menu-submenu-title:hover) {
    color: #2b2b2b !important;
}

.menu :global(.ant-menu-item-icon) {
    font-size: 18px;
}

/* ===== MOBILE RESPONSIVE ===== */
@media (max-width: 768px) {
    .logo {
        height: 64px;
        padding: 12px;
    }

    .logoIcon {
        width: 40px;
        height: 40px;
        font-size: 20px;
        border-radius: 10px;
    }

    .logoTitle {
        font-size: 14px;
    }

    .logoSubtitle {
        font-size: 11px;
    }

    .menu :global(.ant-menu-item),
    .menu :global(.ant-menu-submenu-title) {
        font-size: 14px;
        height: 42px;
        line-height: 42px;
        margin: 3px 6px;
    }

    .menu :global(.ant-menu-item-icon) {
        font-size: 16px;
    }
}

@media (max-width: 480px) {
    .logo {
        height: 56px;
        padding: 8px;
    }

    .logoFull {
        gap: 8px;
    }

    .logoIcon {
        width: 36px;
        height: 36px;
        font-size: 18px;
    }

    .menu :global(.ant-menu-item),
    .menu :global(.ant-menu-submenu-title) {
        font-size: 13px;
        height: 38px;
        line-height: 38px;
        margin: 2px 4px;
    }
}