.container {
    width: 100%;
}

.card {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
}

.card :global(.ant-card-head) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.cardTitle {
    color: #2b2b2b;
    font-size: 18px;
    font-weight: 600;
}

.calendar :global(.ant-picker-calendar) {
    background: transparent;
}

.calendar :global(.ant-picker-panel) {
    background: transparent;
}

.calendar :global(.ant-picker-calendar-header) {
    color: #2b2b2b;
}

.calendar :global(.ant-picker-cell) {
    color: #2b2b2b;
}

/* Responsive breakpoints */
@media (max-width: 768px) {
    .cardTitle {
        font-size: 16px;
    }

    .calendar :global(.ant-picker-calendar) {
        font-size: 12px;
    }

    .calendar :global(.ant-picker-cell) {
        padding: 4px;
    }
}

@media (max-width: 576px) {
    .card {
        border-radius: 8px;
    }

    .cardTitle {
        font-size: 14px;
    }

    .calendar :global(.ant-picker-calendar) {
        font-size: 11px;
    }

    .calendar :global(.ant-picker-cell) {
        padding: 2px;
    }
}