.header {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 0 24px;
    display: flex;
    align-items: center;
}

.headerContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.menuToggle {
    font-size: 20px;
    width: 64px;
    height: 64px;
    color: #fff;
}

.menuToggle:hover {
    color: #1890ff !important;
    background: rgba(255, 255, 255, 0.1) !important;
}

.headerIcon {
    color: #fff;
    font-size: 20px;
}

.headerIcon:hover {
    color: #1890ff !important;
    background: rgba(255, 255, 255, 0.1) !important;
}

.side {
    background: rgba(255, 255, 255, 0.5) !important;
    backdrop-filter: blur(30px);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    height: 100%;
}

.logo {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    margin-bottom: 16px;
}

.logoContent {
    display: flex;
    align-items: center;
    width: 100%;
}

.menu {
    background: transparent !important;
    border-right: none !important;
}

.menu :global(.ant-menu-item),
.menu :global(.ant-menu-submenu-title) {
    color: rgba(255, 255, 255, 0.85) !important;
    font-size: 15px;
    margin: 4px 8px;
    border-radius: 8px;
    height: 44px;
    line-height: 44px;
}

.menu :global(.ant-menu-item-selected) {
    background: linear-gradient(135deg, #1890ff 0%, #096dd9 100%) !important;
    color: #fff !important;
}

.menu :global(.ant-menu-item:hover),
.menu :global(.ant-menu-submenu-title:hover) {
    background: rgba(255, 255, 255, 0.1) !important;
    color: #fff !important;
}

.menu :global(.ant-menu-submenu-selected)> :global(.ant-menu-submenu-title) {
    color: #1890ff !important;
}

.menu :global(.ant-menu-item-icon) {
    font-size: 18px;
}

.menu :global(.ant-menu-sub) {
    background: rgba(0, 0, 0, 0.2) !important;
}

.antLayout {
    background: transparent !important;
}

.content {
    margin: 24px 16px;
    padding: 24px;
    min-height: 280px;
    background: transparent;
    border-radius: 10px;
}

/* Scrollbar styling */
.side :global(::-webkit-scrollbar) {
    width: 6px;
}

.side :global(::-webkit-scrollbar-track) {
    background: rgba(255, 255, 255, 0.05);
}

.side :global(::-webkit-scrollbar-thumb) {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
}

.side :global(::-webkit-scrollbar-thumb:hover) {
    background: rgba(255, 255, 255, 0.3);
}

/* Mobile overlay */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.45);
    z-index: 999;
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

/* Mobile sidebar states */
.sideMobileOpen {
    position: fixed !important;
    left: 0 !important;
    top: 0 !important;
    bottom: 0 !important;
    z-index: 1000 !important;
    transform: translateX(0) !important;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
    width: 280px !important;
    min-width: 280px !important;
    max-width: 280px !important;
    flex: 0 0 280px !important;
}

.sideMobileHidden {
    position: fixed !important;
    left: 0 !important;
    top: 0 !important;
    bottom: 0 !important;
    z-index: 1000 !important;
    transform: translateX(-100%) !important;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
    width: 0 !important;
    min-width: 0 !important;
    max-width: 0 !important;
    flex: 0 0 0px !important;
    overflow: hidden !important;
}

/* ===== MOBILE RESPONSIVE ===== */
@media (max-width: 768px) {
    .header {
        padding: 0 12px;
        position: sticky;
        top: 0;
        z-index: 998;
    }

    .menuToggle {
        width: 48px;
        height: 48px;
        font-size: 18px;
    }

    .content {
        margin: 12px 8px;
        padding: 12px;
        min-height: auto;
    }

    .logo {
        height: 64px;
        padding: 12px;
    }

    .menu :global(.ant-menu-item),
    .menu :global(.ant-menu-submenu-title) {
        font-size: 14px;
        height: 40px;
        line-height: 40px;
        margin: 3px 6px;
    }
}

@media (max-width: 480px) {
    .header {
        padding: 0 8px;
    }

    .content {
        margin: 8px 4px;
        padding: 8px;
    }

    .menuToggle {
        width: 40px;
        height: 40px;
        font-size: 16px;
    }
}