.header {
    height: 64px;
    background: rgba(247, 247, 247, 0.493);
    backdrop-filter: blur(30px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.menuToggle {
    font-size: 20px;
    width: 64px;
    height: 64px;
    color: #2b2b2b;
}

.menuToggle:hover {
    color: #1890ff !important;
    background: rgba(255, 255, 255, 0.1) !important;
}

.rightSection {
    display: flex;
    align-items: center;
}

.iconButton {
    color: #2b2b2b;
    font-size: 20px;
    width: 40px;
    height: 40px;
}

.iconButton:hover {
    color: #1890ff !important;
    background: rgba(255, 255, 255, 0.1) !important;
}

.userInfo {
    cursor: pointer;
    padding: 4px 12px;
    border-radius: 8px;
    transition: all 0.3s;
}

.userInfo:hover {
    background: rgba(255, 255, 255, 0.1);
}

.userText {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.userName {
    color: #2b2b2b !important;
    font-size: 14px;
    line-height: 1.2;
}

.userRole {
    color: #2b2b2b !important;
    font-size: 12px;
    line-height: 1.2;
}

@media (max-width: 768px) {
    .header {
        padding: 0 16px;
    }

    .userText {
        display: none;
    }

    .userInfo {
        padding: 0;
    }

    .userInfo:hover {
        background: transparent;
    }
}

@media (max-width: 576px) {
    .header {
        padding: 0 12px;
    }

    .menuToggle {
        font-size: 18px;
        width: 48px;
        height: 64px;
    }

    .rightSection {
        gap: 12px;
    }
}