.container {
    width: 100%;
    padding: 10px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
}

.header h2 {
    margin: 0;
    font-size: 22px;
    font-weight: 700;
    color: #1a1a2e;
}

.statsRow {
    display: flex;
    gap: 12px;
    margin-bottom: 24px;
    flex-wrap: wrap;
}

.statCard {
    flex: 1;
    min-width: 120px;
    padding: 16px;
    border-radius: 12px;
    text-align: center;
    background: #fff;
    box-shadow: 0 2px 8px rgba(0,0,0,0.06);
}

.statCard h3 {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
}

.statCard p {
    margin: 4px 0 0;
    font-size: 12px;
    color: #888;
}

/* Order Cards */
.orderCard {
    border-radius: 14px !important;
    overflow: hidden;
    transition: transform 0.2s, box-shadow 0.2s;
    border: none !important;
    box-shadow: 0 2px 12px rgba(0,0,0,0.06);
}

.orderCard:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 20px rgba(0,0,0,0.1);
}

.orderCardHeader {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 12px;
}

.orderCardClient {
    display: flex;
    align-items: center;
    gap: 10px;
}

.orderCardClient h4 {
    margin: 0;
    font-size: 15px;
    font-weight: 600;
    color: #1a1a2e;
}

.orderCardClient span {
    font-size: 12px;
    color: #888;
}

.orderCardBody {
    padding: 4px 0;
}

.orderInfoRow {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 8px;
    font-size: 13px;
    color: #555;
}

.orderInfoRow svg {
    color: #1890ff;
    font-size: 14px;
    flex-shrink: 0;
}

.orderPrice {
    font-size: 18px;
    font-weight: 700;
    color: #52c41a;
    margin: 12px 0 4px;
}

.orderActions {
    display: flex;
    gap: 8px;
    margin-top: 12px;
}

/* Status bar */
.statusBar {
    width: 4px;
    border-radius: 4px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
}

/* Modal */
.modalSection {
    margin-bottom: 24px;
}

.modalSection h3 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 12px;
    color: #1a1a2e;
    display: flex;
    align-items: center;
    gap: 8px;
}

.infoGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
}

.infoItem {
    padding: 10px 14px;
    background: #f8f9fb;
    border-radius: 10px;
}

.infoItem label {
    font-size: 11px;
    color: #999;
    display: block;
    margin-bottom: 2px;
}

.infoItem span {
    font-size: 14px;
    font-weight: 600;
    color: #333;
}

.listItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 14px;
    background: #f8f9fb;
    border-radius: 10px;
    margin-bottom: 8px;
}

.listItem:last-child {
    margin-bottom: 0;
}

.addRow {
    display: flex;
    gap: 8px;
    margin-top: 10px;
}

@media (max-width: 768px) {
    .infoGrid {
        grid-template-columns: 1fr;
    }

    .statsRow {
        gap: 8px;
    }

    .statCard {
        min-width: 80px;
        padding: 12px;
    }

    .statCard h3 {
        font-size: 18px;
    }
}