.restgrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 16px;
    align-items: stretch;
}

.restgrid a {
    text-decoration: none;
}

@media (max-width: 960px) {
    .blockleft {
        width: 100%;
        left: 0;
        right: 0;
        top: 80px;
        height: auto;
        bottom: 70px;
    }

    .blocbottom {
        position: fixed;
        bottom: 0;
    }

    .filterContainer {
        position: fixed;
        top: -500px;
        left: 0;
        right: 0;
        z-index: 1000;
        transition: 0.3s ease-in-out;
    }
    
    .filterContainerActive {
        position: fixed;
        top: 100px;
        left: 0;
        right: 0;
        z-index: 1000;
        transition: 0.3s ease-in-out;
    }
}