.container {
    min-height: 100vh;
    padding: 24px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 100px;
}

.profileCard {
    width: 100%;
    max-width: 600px;
    border-radius: 16px;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
}

.header {
    display: flex;
    align-items: center;
    gap: 20px;
}

.avatar {
    background: linear-gradient(135deg, #1890ff 0%, #722ed1 100%);
    flex-shrink: 0;
}

.userInfo {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.userName {
    margin: 0 !important;
    color: #2a2a2a;
}

.roleTag {
    width: fit-content;
    font-size: 12px;
}

.infoSection {
    margin-top: 16px;
}

.infoItem {
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 12px;
    background: #f9f9f9;
    border-radius: 8px;
}

.actions {
    display: flex;
    gap: 12px;
    margin-top: 24px;
}

@media (max-width: 576px) {
    .container {
        padding: 16px;
        padding-top: 10px;
    }

    .header {
        flex-direction: column;
        text-align: center;
    }

    .userInfo {
        align-items: center;
    }

    .actions {
        flex-direction: column;
    }

    .actions button {
        width: 100%;
    }
}
